@import url("https://fonts.googleapis.com/css2?family=Ubuntu&display=swap");
@import "./variable.css";

html {
  font-size: 10px;
  font-family: "Ubuntu", sans-serif;
}

body {
      display: block;
      height: 100vh;
      width: 100%;
  font-size: 1.5rem;
  background-color: var(--c-white);
  color: var(--c-text);
  box-sizing: border-box;
}

a{
  color:var(--c-a);

  &:hover{
    color:var(--c-a-hover);
    cursor:pointer;
  }
}