@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);

.light{--c-white:#fff;--c-black:#000;--c-text: #7e7e7e;--c-text-hover: #252525;--c-a: #252525;--c-a-hover: #044cd0}.dark{--c-primary: #222831;--c-hover: #393e46;--c-border: #00adb5;--c-text: #eeeeee}

html{font-size:10px;font-family:"Ubuntu", sans-serif}body{display:block;height:100vh;width:100%;font-size:1.5rem;background-color:var(--c-white);color:var(--c-text);box-sizing:border-box}a{color:var(--c-a)}a:hover{color:var(--c-a-hover);cursor:pointer}

